'use client';

import React, { useEffect, useRef, useState } from 'react';

interface ThreeDViewerProps {
  modelUrl?: string;
  className?: string;
}

const ThreeDViewer: React.FC<ThreeDViewerProps> = ({ modelUrl, className = '' }) => {
  const mountRef = useRef<HTMLDivElement>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [controls, setControls] = useState({
    rotationX: 0,
    rotationY: 0,
    zoom: 1,
  });

  useEffect(() => {
    if (!mountRef.current) return;

    // For now, we'll create a simple 3D-like visualization
    // In a real implementation, you would use Three.js here
    setIsLoading(false);
    
    // Simulate loading time
    const timer = setTimeout(() => {
      setIsLoading(false);
    }, 1000);

    return () => clearTimeout(timer);
  }, [modelUrl]);

  const handleRotateLeft = () => {
    setControls(prev => ({
      ...prev,
      rotationY: prev.rotationY - 15
    }));
  };

  const handleRotateRight = () => {
    setControls(prev => ({
      ...prev,
      rotationY: prev.rotationY + 15
    }));
  };

  const handleRotateUp = () => {
    setControls(prev => ({
      ...prev,
      rotationX: Math.max(prev.rotationX - 15, -90)
    }));
  };

  const handleRotateDown = () => {
    setControls(prev => ({
      ...prev,
      rotationX: Math.min(prev.rotationX + 15, 90)
    }));
  };

  const handleZoomIn = () => {
    setControls(prev => ({
      ...prev,
      zoom: Math.min(prev.zoom + 0.1, 2)
    }));
  };

  const handleZoomOut = () => {
    setControls(prev => ({
      ...prev,
      zoom: Math.max(prev.zoom - 0.1, 0.5)
    }));
  };

  const handleReset = () => {
    setControls({
      rotationX: 0,
      rotationY: 0,
      zoom: 1,
    });
  };

  if (error) {
    return (
      <div className={`bg-gray-100 rounded-lg flex items-center justify-center ${className}`}>
        <div className="text-center p-8">
          <div className="text-red-500 mb-2">3D Viewer Error</div>
          <p className="text-gray-600 text-sm">{error}</p>
        </div>
      </div>
    );
  }

  return (
    <div className={`bg-gradient-to-br from-gray-50 to-gray-100 rounded-lg overflow-hidden ${className}`}>
      {/* 3D Viewer Area */}
      <div className="relative h-96 flex items-center justify-center">
        {isLoading ? (
          <div className="text-center">
            <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-sea-blue mb-4"></div>
            <p className="text-gray-600">Loading 3D Model...</p>
          </div>
        ) : (
          <div className="relative">
            {/* Simulated 3D Object */}
            <div
              className="w-48 h-48 bg-gradient-to-br from-sea-blue to-royal-blue rounded-lg shadow-2xl transition-transform duration-300 ease-in-out"
              style={{
                transform: `
                  perspective(1000px) 
                  rotateX(${controls.rotationX}deg) 
                  rotateY(${controls.rotationY}deg) 
                  scale(${controls.zoom})
                `,
                transformStyle: 'preserve-3d',
              }}
            >
              {/* Front face */}
              <div className="absolute inset-0 bg-gradient-to-br from-sea-blue to-royal-blue rounded-lg flex items-center justify-center text-app-white font-bold">
                3D Model
              </div>
              
              {/* Side faces for depth effect */}
              <div 
                className="absolute inset-0 bg-gradient-to-br from-royal-blue to-dark-royal-blue rounded-lg opacity-80"
                style={{ transform: 'rotateY(90deg) translateZ(96px)' }}
              ></div>
              <div 
                className="absolute inset-0 bg-gradient-to-br from-dark-royal-blue to-sea-blue rounded-lg opacity-60"
                style={{ transform: 'rotateX(90deg) translateZ(96px)' }}
              ></div>
            </div>

            {/* Model Info Overlay */}
            <div className="absolute top-2 left-2 bg-app-white bg-opacity-90 px-2 py-1 rounded text-xs text-gray-700">
              Interactive 3D View
            </div>
          </div>
        )}
      </div>

      {/* Controls */}
      <div className="p-4 bg-app-white border-t border-gray-200">
        <div className="flex flex-wrap gap-2 justify-center">
          {/* Rotation Controls */}
          <div className="flex gap-1">
            <button
              onClick={handleRotateLeft}
              className="px-3 py-1 bg-sea-blue hover:bg-light-sea-blue text-app-white rounded text-sm font-medium transition-colors"
              title="Rotate Left"
            >
              ← Left
            </button>
            <button
              onClick={handleRotateRight}
              className="px-3 py-1 bg-sea-blue hover:bg-light-sea-blue text-app-white rounded text-sm font-medium transition-colors"
              title="Rotate Right"
            >
              Right →
            </button>
          </div>

          <div className="flex gap-1">
            <button
              onClick={handleRotateUp}
              className="px-3 py-1 bg-royal-blue hover:bg-dark-royal-blue text-app-white rounded text-sm font-medium transition-colors"
              title="Rotate Up"
            >
              ↑ Up
            </button>
            <button
              onClick={handleRotateDown}
              className="px-3 py-1 bg-royal-blue hover:bg-dark-royal-blue text-app-white rounded text-sm font-medium transition-colors"
              title="Rotate Down"
            >
              ↓ Down
            </button>
          </div>

          {/* Zoom Controls */}
          <div className="flex gap-1">
            <button
              onClick={handleZoomIn}
              className="px-3 py-1 bg-gray-600 hover:bg-gray-700 text-app-white rounded text-sm font-medium transition-colors"
              title="Zoom In"
            >
              + Zoom
            </button>
            <button
              onClick={handleZoomOut}
              className="px-3 py-1 bg-gray-600 hover:bg-gray-700 text-app-white rounded text-sm font-medium transition-colors"
              title="Zoom Out"
            >
              - Zoom
            </button>
          </div>

          {/* Reset */}
          <button
            onClick={handleReset}
            className="px-3 py-1 bg-gray-500 hover:bg-gray-600 text-app-white rounded text-sm font-medium transition-colors"
            title="Reset View"
          >
            Reset
          </button>
        </div>

        {/* Current Values Display */}
        <div className="mt-3 text-xs text-gray-500 text-center">
          Rotation: X{controls.rotationX}° Y{controls.rotationY}° | Zoom: {(controls.zoom * 100).toFixed(0)}%
        </div>
      </div>

      {/* Instructions */}
      <div className="px-4 pb-4">
        <p className="text-xs text-gray-500 text-center">
          Use the controls above to rotate and zoom the 3D model
        </p>
      </div>
    </div>
  );
};

export default ThreeDViewer;
