'use client';

import React from 'react';
import Link from 'next/link';
import { useUser } from '@/context/UserContext';
import { useCurrency } from '@/context/CurrencyContext';

const Navigation: React.FC = () => {
  const { user, logout } = useUser();
  const { selectedCurrency, currencyRates, setCurrency } = useCurrency();

  return (
    <nav className="bg-sea-blue text-app-white shadow-lg">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Logo */}
          <div className="flex-shrink-0">
            <Link href="/" className="text-2xl font-bold">
              SecureCommerce
            </Link>
          </div>

          {/* Main Navigation */}
          <div className="hidden md:block">
            <div className="ml-10 flex items-baseline space-x-4">
              <Link
                href="/products"
                className="hover:bg-light-sea-blue px-3 py-2 rounded-md text-sm font-medium transition-colors"
              >
                Products
              </Link>
              <Link
                href="/store-directions"
                className="hover:bg-light-sea-blue px-3 py-2 rounded-md text-sm font-medium transition-colors"
              >
                Store Locations
              </Link>
              {user?.role === 'seller' && (
                <Link
                  href="/seller"
                  className="hover:bg-light-sea-blue px-3 py-2 rounded-md text-sm font-medium transition-colors"
                >
                  Seller Dashboard
                </Link>
              )}
            </div>
          </div>

          {/* Currency Selector */}
          <div className="flex items-center space-x-4">
            <select
              value={selectedCurrency}
              onChange={(e) => setCurrency(e.target.value)}
              className="bg-royal-blue text-app-white px-3 py-1 rounded-md text-sm border-none focus:outline-none focus:ring-2 focus:ring-app-white"
            >
              {currencyRates.map((currency) => (
                <option key={currency.code} value={currency.code}>
                  {currency.code} ({currency.symbol})
                </option>
              ))}
            </select>

            {/* User Menu */}
            {user ? (
              <div className="flex items-center space-x-4">
                <span className="text-sm">Welcome, {user.name}</span>
                {user.role === 'buyer' && (
                  <Link
                    href="/profile"
                    className="hover:bg-light-sea-blue px-3 py-2 rounded-md text-sm font-medium transition-colors"
                  >
                    Profile
                  </Link>
                )}
                <button
                  onClick={logout}
                  className="bg-royal-blue hover:bg-dark-royal-blue px-4 py-2 rounded-md text-sm font-medium transition-colors"
                >
                  Logout
                </button>
              </div>
            ) : (
              <div className="flex items-center space-x-2">
                <Link
                  href="/auth/login"
                  className="hover:bg-light-sea-blue px-3 py-2 rounded-md text-sm font-medium transition-colors"
                >
                  Login
                </Link>
                <Link
                  href="/auth/signup"
                  className="bg-royal-blue hover:bg-dark-royal-blue px-4 py-2 rounded-md text-sm font-medium transition-colors"
                >
                  Sign Up
                </Link>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Mobile menu */}
      <div className="md:hidden">
        <div className="px-2 pt-2 pb-3 space-y-1 sm:px-3 bg-light-sea-blue">
          <Link
            href="/products"
            className="block hover:bg-sea-blue px-3 py-2 rounded-md text-base font-medium transition-colors"
          >
            Products
          </Link>
          <Link
            href="/store-directions"
            className="block hover:bg-sea-blue px-3 py-2 rounded-md text-base font-medium transition-colors"
          >
            Store Locations
          </Link>
          {user?.role === 'seller' && (
            <Link
              href="/seller"
              className="block hover:bg-sea-blue px-3 py-2 rounded-md text-base font-medium transition-colors"
            >
              Seller Dashboard
            </Link>
          )}
        </div>
      </div>
    </nav>
  );
};

export default Navigation;
