'use client';

import React, { useState, useEffect } from 'react';
import { useParams, useRouter } from 'next/navigation';
import Navigation from '@/components/Navigation';
import ThreeDViewer from '@/components/ThreeDViewer';
import { Product } from '@/lib/types';
import { useCurrency } from '@/context/CurrencyContext';
import { useUser } from '@/context/UserContext';

const ProductDetailPage: React.FC = () => {
  const params = useParams();
  const router = useRouter();
  const { user } = useUser();
  const { convertPrice, getCurrencySymbol } = useCurrency();
  
  const [product, setProduct] = useState<Product | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedImageIndex, setSelectedImageIndex] = useState(0);
  const [quantity, setQuantity] = useState(1);
  const [showAddToCart, setShowAddToCart] = useState(false);

  useEffect(() => {
    if (params.id) {
      fetchProduct(params.id as string);
    }
  }, [params.id]);

  const fetchProduct = async (productId: string) => {
    try {
      setLoading(true);
      const response = await fetch('/api/products');
      
      if (!response.ok) {
        throw new Error('Failed to fetch product');
      }

      const data = await response.json();
      const foundProduct = data.products.find((p: Product) => p.id === productId);
      
      if (!foundProduct) {
        setError('Product not found');
        return;
      }

      setProduct(foundProduct);
      setError(null);
    } catch (err) {
      setError('Failed to load product. Please try again.');
      console.error('Error fetching product:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleAddToCart = () => {
    if (!user) {
      router.push('/auth/login');
      return;
    }
    
    // Simulate add to cart
    setShowAddToCart(true);
    setTimeout(() => setShowAddToCart(false), 3000);
  };

  const handleBuyNow = () => {
    if (!user) {
      router.push('/auth/login');
      return;
    }
    
    // Navigate to checkout with this product
    router.push(`/checkout?productId=${product?.id}&quantity=${quantity}`);
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-app-white to-gray-50">
        <Navigation />
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="text-center py-12">
            <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-sea-blue"></div>
            <p className="mt-4 text-gray-600">Loading product...</p>
          </div>
        </div>
      </div>
    );
  }

  if (error || !product) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-app-white to-gray-50">
        <Navigation />
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="text-center py-12">
            <div className="bg-red-50 border border-red-200 rounded-md p-4 max-w-md mx-auto">
              <p className="text-red-600">{error || 'Product not found'}</p>
              <button
                onClick={() => router.push('/products')}
                className="mt-4 px-4 py-2 bg-sea-blue hover:bg-light-sea-blue text-app-white rounded-md text-sm font-medium transition-colors"
              >
                Back to Products
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  const convertedPrice = convertPrice(product.priceUSD);

  return (
    <div className="min-h-screen bg-gradient-to-br from-app-white to-gray-50">
      <Navigation />
      
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Breadcrumb */}
        <nav className="mb-8">
          <ol className="flex items-center space-x-2 text-sm text-gray-500">
            <li>
              <button onClick={() => router.push('/')} className="hover:text-sea-blue transition-colors">
                Home
              </button>
            </li>
            <li>/</li>
            <li>
              <button onClick={() => router.push('/products')} className="hover:text-sea-blue transition-colors">
                Products
              </button>
            </li>
            <li>/</li>
            <li className="text-gray-900 font-medium">{product.title}</li>
          </ol>
        </nav>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-8">
          {/* Product Images and 3D Viewer */}
          <div className="space-y-4">
            {/* Main Image */}
            <div className="bg-app-white rounded-xl shadow-lg border border-gray-100 overflow-hidden">
              <div className="aspect-square">
                {product.images && product.images.length > 0 ? (
                  <img
                    src={product.images[selectedImageIndex]}
                    alt={product.title}
                    className="w-full h-full object-cover"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement;
                      target.src = 'data:image/svg+xml;base64,';
                    }}
                  />
                ) : (
                  <div className="w-full h-full flex items-center justify-center bg-gray-100">
                    <span className="text-gray-400">No Image Available</span>
                  </div>
                )}
              </div>
            </div>

            {/* Image Thumbnails */}
            {product.images && product.images.length > 1 && (
              <div className="flex space-x-2 overflow-x-auto">
                {product.images.map((image, index) => (
                  <button
                    key={index}
                    onClick={() => setSelectedImageIndex(index)}
                    className={`flex-shrink-0 w-20 h-20 rounded-lg overflow-hidden border-2 transition-colors ${
                      selectedImageIndex === index
                        ? 'border-sea-blue'
                        : 'border-gray-200 hover:border-gray-300'
                    }`}
                  >
                    <img
                      src={image}
                      alt={`${product.title} ${index + 1}`}
                      className="w-full h-full object-cover"
                    />
                  </button>
                ))}
              </div>
            )}

            {/* 3D Viewer */}
            {product.model3DUrl && (
              <div className="bg-app-white rounded-xl shadow-lg border border-gray-100 overflow-hidden">
                <div className="p-4 border-b border-gray-100">
                  <h3 className="text-lg font-semibold text-sea-blue">3D Interactive View</h3>
                  <p className="text-sm text-gray-600">Explore the product in 3D</p>
                </div>
                <ThreeDViewer modelUrl={product.model3DUrl} />
              </div>
            )}
          </div>

          {/* Product Information */}
          <div className="space-y-6">
            <div className="bg-app-white p-6 rounded-xl shadow-lg border border-gray-100">
              {/* Category */}
              <div className="mb-2">
                <span className="text-sm text-gray-500 uppercase tracking-wide">
                  {product.category}
                </span>
              </div>

              {/* Title */}
              <h1 className="text-3xl font-bold text-gray-900 mb-4">
                {product.title}
              </h1>

              {/* Price */}
              <div className="text-4xl font-bold text-sea-blue mb-6">
                {getCurrencySymbol()}{convertedPrice.toFixed(2)}
              </div>

              {/* Stock Status */}
              <div className="mb-6">
                {product.stock > 0 ? (
                  <div className="flex items-center space-x-2">
                    <div className="w-3 h-3 bg-green-500 rounded-full"></div>
                    <span className="text-green-600 font-medium">
                      In Stock ({product.stock} available)
                    </span>
                  </div>
                ) : (
                  <div className="flex items-center space-x-2">
                    <div className="w-3 h-3 bg-red-500 rounded-full"></div>
                    <span className="text-red-600 font-medium">Out of Stock</span>
                  </div>
                )}
              </div>

              {/* Quantity Selector */}
              {product.stock > 0 && (
                <div className="mb-6">
                  <label htmlFor="quantity" className="block text-sm font-medium text-gray-700 mb-2">
                    Quantity
                  </label>
                  <select
                    id="quantity"
                    value={quantity}
                    onChange={(e) => setQuantity(parseInt(e.target.value))}
                    className="w-24 px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-sea-blue focus:border-sea-blue"
                  >
                    {Array.from({ length: Math.min(product.stock, 10) }, (_, i) => i + 1).map((num) => (
                      <option key={num} value={num}>
                        {num}
                      </option>
                    ))}
                  </select>
                </div>
              )}

              {/* Action Buttons */}
              <div className="space-y-3">
                {product.stock > 0 ? (
                  <>
                    <button
                      onClick={handleBuyNow}
                      className="w-full bg-sea-blue hover:bg-light-sea-blue text-app-white py-3 px-6 rounded-lg text-lg font-semibold transition-colors"
                    >
                      Buy Now
                    </button>
                    <button
                      onClick={handleAddToCart}
                      className="w-full bg-royal-blue hover:bg-dark-royal-blue text-app-white py-3 px-6 rounded-lg text-lg font-semibold transition-colors"
                    >
                      Add to Cart
                    </button>
                  </>
                ) : (
                  <button
                    disabled
                    className="w-full bg-gray-400 text-app-white py-3 px-6 rounded-lg text-lg font-semibold cursor-not-allowed"
                  >
                    Out of Stock
                  </button>
                )}
              </div>

              {/* Success Message */}
              {showAddToCart && (
                <div className="mt-4 p-3 bg-green-50 border border-green-200 rounded-md">
                  <p className="text-green-600 text-sm font-medium">
                    Product added to cart successfully!
                  </p>
                </div>
              )}
            </div>

            {/* Product Description */}
            <div className="bg-app-white p-6 rounded-xl shadow-lg border border-gray-100">
              <h2 className="text-xl font-semibold text-gray-900 mb-4">Description</h2>
              <p className="text-gray-600 leading-relaxed">
                {product.description}
              </p>
            </div>

            {/* Product Details */}
            <div className="bg-app-white p-6 rounded-xl shadow-lg border border-gray-100">
              <h2 className="text-xl font-semibold text-gray-900 mb-4">Product Details</h2>
              <dl className="space-y-3">
                <div className="flex justify-between">
                  <dt className="text-gray-600">Product ID:</dt>
                  <dd className="text-gray-900 font-medium">{product.id}</dd>
                </div>
                <div className="flex justify-between">
                  <dt className="text-gray-600">Category:</dt>
                  <dd className="text-gray-900 font-medium">{product.category}</dd>
                </div>
                <div className="flex justify-between">
                  <dt className="text-gray-600">Stock:</dt>
                  <dd className="text-gray-900 font-medium">{product.stock} units</dd>
                </div>
                <div className="flex justify-between">
                  <dt className="text-gray-600">3D View:</dt>
                  <dd className="text-gray-900 font-medium">
                    {product.model3DUrl ? 'Available' : 'Not Available'}
                  </dd>
                </div>
              </dl>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductDetailPage;
